
nav {
  padding: 3px;
  margin-left: 0px;
  font-size: 14px;
  border-bottom: 1px solid;
}
a {
  text-decoration: none;
  color: inherit;
}
li:hover {
  background-color: orange;
  box-sizing: border-box;
}
ol {
  display: inline;
  vertical-align: middle;
  padding: 5px;
}
li {
  display: inline;
}
.left {
  display: inline;
}
.right {
  float: right;
}



